import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './Dashboard.css';
import SimpleChart from './SimpleChart';
import './SimpleChart.css';

const Dashboard = ({ user, onLogout }) => {
  const [transactions, setTransactions] = useState([]);
  const [showTransactionForm, setShowTransactionForm] = useState(false);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    fetchTransactions();
  }, []);

  const fetchTransactions = async () => {
    try {
      const response = await axios.get('http://localhost:5000/api/transactions');
      setTransactions(response.data);
      setLoading(false);
    } catch (err) {
      setError('Failed to fetch transactions');
      setLoading(false);
    }
  };

  const calculateMetrics = () => {
    const income = transactions
      .filter(t => t.type === 'income')
      .reduce((sum, t) => sum + t.amount, 0);
    
    const expenses = transactions
      .filter(t => t.type === 'expense')
      .reduce((sum, t) => sum + t.amount, 0);
    
    const balance = income - expenses;
    
    const categoryData = transactions
      .filter(t => t.type === 'expense')
      .reduce((acc, t) => {
        acc[t.category] = (acc[t.category] || 0) + t.amount;
        return acc;
      }, {});

    const pieData = Object.entries(categoryData).map(([name, value]) => ({
      name,
      value
    }));

    return { income, expenses, balance, pieData };
  };

  const metrics = calculateMetrics();

  return (
    <div className="dashboard">
      <header className="dashboard-header">
        <div className="welcome-section">
          <h1>Hello, {user.name}</h1>
          <p className="balance">Total Balance: ${metrics.balance.toFixed(2)}</p>
          <button className="btn btn-outline-danger btn-sm" onClick={onLogout}>
            Logout
          </button>
        </div>
      </header>

      <div className="metrics-grid">
        <div className="metric-card income">
          <h3>Income</h3>
          <p className="amount">${metrics.income.toFixed(2)}</p>
          <div className="progress">
            <div 
              className="progress-bar" 
              style={{ width: `${(metrics.income / (metrics.income + metrics.expenses)) * 100}%` }}
            />
          </div>
        </div>

        <div className="metric-card expenses">
          <h3>Expenses</h3>
          <p className="amount">${metrics.expenses.toFixed(2)}</p>
          <div className="progress">
            <div 
              className="progress-bar" 
              style={{ width: `${(metrics.expenses / (metrics.income + metrics.expenses)) * 100}%` }}
            />
          </div>
        </div>

        <div className="metric-card goal">
          <h3>Current Goal</h3>
          <p>Sony Headphones</p>
          <div className="goal-progress">
            <div className="progress">
              <div className="progress-bar" style={{ width: '80%' }} />
            </div>
            <p>$200/$250</p>
          </div>
        </div>

        <div className="metric-card account">
          <h3>Visa Debit</h3>
          <p className="amount">$900.00</p>
          <button className="btn btn-link">View Details</button>
        </div>
      </div>

      <div className="dashboard-content">
        <div className="content-header">
          <h2>Recent Transactions</h2>
          <button 
            className="btn btn-primary"
            onClick={() => setShowTransactionForm(true)}
          >
            Add Transaction
          </button>
        </div>

        {showTransactionForm && (
          <div className="transaction-form-modal">
            {/* Add your TransactionForm component here */}
          </div>
        )}

        <div className="transactions-table">
          <table className="table">
            <thead>
              <tr>
                <th>Date</th>
                <th>Category</th>
                <th>Amount</th>
                <th>Type</th>
              </tr>
            </thead>
            <tbody>
              {transactions.slice(0, 5).map((transaction) => (
                <tr key={transaction.id}>
                  <td>{new Date(transaction.date).toLocaleDateString()}</td>
                  <td>{transaction.category}</td>
                  <td>${transaction.amount.toFixed(2)}</td>
                  <td>{transaction.type}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        <div className="charts-section">
          <div className="spending-chart">
            <h3>Spending by Category</h3>
            {metrics.pieData.length > 0 ? (
              <SimpleChart pieData={metrics.pieData} />
            ) : (
              <div className="no-data-message">
                {loading ? 'Loading chart data...' : 'No spending data available'}
              </div>
            )}
          </div>
        </div>
      </div>

      <nav className="bottom-nav">
        <button className="nav-item active">
          <i className="fas fa-home"></i>
          <span>Dashboard</span>
        </button>
        <button className="nav-item">
          <i className="fas fa-plus"></i>
          <span>Add</span>
        </button>
        <button className="nav-item">
          <i className="fas fa-wallet"></i>
          <span>Wallet</span>
        </button>
      </nav>
    </div>
  );
};

export default Dashboard; 